import { Trans } from '@lingui/macro'
import { Dialog, DialogTitle } from '@material-ui/core'
import { useRouterContext } from '@speedlo/hooks'
import React from 'react'
import resolvePathname from 'resolve-pathname'

import { WareCategoryTypeEnum } from '../../../graph/generated'
import { TouchButton } from '../../../molecules/TouchButton'
import { getRecipeRoute } from '../models/MenuRecipeModel'
import { CreateButton } from './CreateButton'

export const RecipeTypeSelection: React.FC = () => {
  const [isOpen, setOpen] = React.useState(false)
  const { history } = useRouterContext()

  const onSelect = (recipeType: WareCategoryTypeEnum) => () => {
    const to = getRecipeRoute(
      resolvePathname('create', `${location.pathname}/`),
      recipeType,
    )
    history.push(to)
  }

  return (
    <>
      <CreateButton
        onClick={ev => {
          ev.preventDefault()
          setOpen(true)
        }}
      />
      <Dialog open={isOpen} onClose={() => setOpen(false)}>
        <DialogTitle>Select item type</DialogTitle>
        <TouchButton onClick={onSelect(WareCategoryTypeEnum.Main)}>
          <Trans>Ware</Trans>
        </TouchButton>
        <TouchButton onClick={onSelect(WareCategoryTypeEnum.SideDish)}>
          <Trans>Sidedish</Trans>
        </TouchButton>
        <TouchButton onClick={onSelect(WareCategoryTypeEnum.Cover)}>
          <Trans>Cover</Trans>
        </TouchButton>
      </Dialog>
    </>
  )
}
